import * as React from "react";
import { TextField, Button, Box } from "@material-ui/core";
import { gql } from "apollo-boost";
import { useMutation } from "@apollo/react-hooks";
import { useSnackbar } from "notistack";

const SEND_EMAIL_OPT = gql`
  mutation($email: String!) {
    send_email_opt(email: $email)
  }
`;

const ADD_USER_WITH_CODE = gql`
  mutation(
    $username: String!
    $password: String!
    $email: String!
    $code: String!
  ) {
    add_user_with_code(
      user: {
        username: $username
        password: $password
        email: $email
        code: $code
      }
    ) {
      uid
    }
  }
`;

export default function SignIn() {
  const { enqueueSnackbar } = useSnackbar();
  const [username, setUsername] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [code, setCode] = React.useState("");

  // 
  const [send_email_opt] = useMutation(SEND_EMAIL_OPT, {
    onCompleted(data) {
      console.log(data);
      enqueueSnackbar("");
    },
    onError(error) {
      console.error(error);
      enqueueSnackbar("");
    }
  });
  const _sendVerifyCode = function() {
    send_email_opt({ variables: { email: email } });
  };

  // 
  const [add_user_with_code] = useMutation(ADD_USER_WITH_CODE, {
    onError(error) {
      console.error(error);
      enqueueSnackbar("");
    },
    onCompleted(data) {
      console.log(data);
      enqueueSnackbar("");
    }
  });
  const _signUp = function() {
    add_user_with_code({
      variables: {
        username,
        password,
        email,
        code
      }
    });
  };

  return (
    <div>
      <form>
        <TextField
          id="username"
          label=""
          required
          value={username}
          onChange={e => setUsername(e.target.value)}
          placeholder=""
          fullWidth
          margin="normal"
        />
        <TextField
          id="password"
          label=""
          type="password"
          required
          value={password}
          onChange={e => setPassword(e.target.value)}
          placeholder=""
          fullWidth
          margin="normal"
        />
        <TextField
          id="email"
          label=""
          type="email"
          required
          value={email}
          onChange={e => setEmail(e.target.value)}
          placeholder=""
          fullWidth
          margin="normal"
        />
        <Box>
          <TextField
            id="code"
            label=""
            value={code}
            onChange={e => setCode(e.target.value)}
            placeholder=""
            margin="normal"
          />
          <Button variant="contained" onClick={_sendVerifyCode}>
            
          </Button>
        </Box>
        <Button variant="contained" fullWidth onClick={_signUp}>
          
        </Button>
      </form>
    </div>
  );
}
